* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
@media screen and (max-width: 380px) {
  form {
    width: 300px;
  }
  p {
    font-size: 16px;
  }
  label {
    font-size: 12px;
  }
}
form {
  width: 500px;
  margin: 5% auto;
  padding: 30px;
  box-shadow: 1px 1px 9px rgb(100, 88, 88);
  border: none;
  border-radius: 20px;
}
p {
  font-size: 20px;
  margin-bottom: 30px;
  text-transform: uppercase;
}
.text {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;
}
form div {
  margin-bottom: 20px;
}
input[type="radio"] {
  width: auto;
}
span {
  margin-right: 30px;
  margin-left: 10px;
}
input,
textarea {
  border: 1px solid #000;
  border-radius: 6px;
  padding: 10px;
  width: 100%;
}
label {
  display: block;
  font-size: 18px;
  margin-bottom: 5px;
}

input[type="submit"]{
  border:none;
  outline:none;
  background-color: rgb(133, 175, 133);
  color:white;
  font-size: 18px;
  letter-spacing: 5px;
  cursor:pointer
}